import React from 'react';
import { NavLink } from 'react-router-dom';
import userPhoto from '../../img/smallProfilePhoto.png'

const User = (props) => {
    return (
        <div>
            <NavLink to={`/profile/${props.id}`}>
                <img className = 'user__photo'
                src = {props.photos.small != null ? props.photos.small : userPhoto}/>
            </NavLink>
            <div>{props.name}</div>
            <div>{props.status}</div>
            <div>{"props.location.city"}</div>
            <div>{"props.location.country"}</div>
            {props.followed
                ? <button disabled = {props.followingInProgress.some(id => id === props.id)} 
                    onClick={() => {props.unfollowThunkCreator(props.id)}}>unfollow</button>
                : <button disabled = {props.followingInProgress.some(id => id === props.id)} 
                    onClick={() => {props.followThunkCreator(props.id)}}>follow</button>}
        </div>
    )
};

export default User;