import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { compose } from 'redux';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { addPost, getProfileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator } from '../../redux/profileReducer';

import Profile from './Profile';


class ProfileAPIComponent extends React.Component {
  componentDidMount() {
    let userId = this.props.match.params.userId
    if(!userId) {
      userId = this.props.userId;
    };
    this.props.getProfileThunkCreator(userId);
    this.props.getStatusThunkCreator(userId);
  }
  
  render() {
    return <Profile {...this.props} profile = {this.props.profile} updateStatus = {this.props.updateStatusThunkCreator}/>
  }


}
const mapStateToProps = (state) => {
  return {
    profile: state.profilePage.profile,
    postsData: state.profilePage.postsData,
    postValue: state.profilePage.newPostText,
    status: state.profilePage.status,
    userId: state.auth.userId,
  };
};

export default compose(
  connect(mapStateToProps, {addPost, getProfileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator}),
  withRouter,
  withAuthRedirect
)(ProfileAPIComponent);