let initialState = {
    dialogsData: [
        {id: 1, name: 'Vasya'},
        {id: 2, name: 'Sveta'},
        {id: 3, name: 'Valera'},
        {id: 4, name: 'Hector'},
    ],
    messagesData: [
        {text: 'Wassup'},
        {text: 'Wanna eat'},
        {text: 'And drink'},
        {text: 'And sleep'},
    ],
};

export const addMessage = (newMessageBody) => ({type: 'ADD_MESSAGE', newMessageBody});

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'ADD_MESSAGE': {
            let newMessage = {
            text: action.newMessageBody,
            };
            return {
                ...state,
                messagesData: [...state.messagesData, newMessage],
            };
        }
        default:
            return state;
    };
};

export default dialogsReducer;
